import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import './plugins/element.js'
import * as filters from '../src/assets/js/formartDate'
// import Qs from 'qs'
import vuescroll from 'vuescroll'
import echarts from 'echarts'

// 清除浏览器默认样式
import '@/assets/css/reset.css'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'

Vue.use(vuescroll, {
  ops: {
    bar: {
      background: '#6472FF'
    }
  } // 在这里设置全局默认配置
})
Vue.use(filters)
function urlString () {
  var href = window.location.search.replace(/^\?/, '')
  var part = href.split('&')
  var obj = {}
  for (var i = 0; i < part.length; i++) {
    var _part = part[i].split('=')
    obj[_part[0]] = _part[1]
  }
  return obj
}

Vue.use(VueAwesomeSwiper)
echarts.registerTheme('pay1', {
  color: [
    '#6472FF',
    '#EE6666'
  ]
})
Vue.prototype.$echarts = echarts
Vue.prototype.api_base_url = 'https://api.i -school.net'
Vue.prototype.id =  urlString().groupId || localStorage.getItem('groupId') || '1124196'
Vue.prototype.sid = urlString().groupId || localStorage.getItem('groupId') || '1124196'
Vue.prototype.provider_id = urlString().provider_id || '10000' 
Vue.prototype.district_id =  urlString().groupId || localStorage.getItem('groupId') || '1124196'

// axios设置引用
const baseURL = process.env.NODE_ENV === 'production' ? 'https://api.i-school.net' : 'https://api.i-school.net'
Vue.prototype.$http = axios.create({
  timeout: 6000,
  withCredentials: true,
})
axios.defaults.headers['Content-Type'] = 'application/json'
Vue.config.productionTip = false
axios.defaults.headers.Accept = '*'
